export default {
    setUser(state, payload) {
       
      state.user = payload.user;
    },
    SetAESKey(state, payload) {
 
       
      state.aesKey = payload.aesKey;
    },
    SetSubscribers(state, payload) {
      
      state.subscribers = payload.subscribers;
     
    },
    setPlan(state,payload){
   
      state.plan=payload;
    }
  };
  