<template>
<div>
    <h2> Create Plan Details</h2>
    {{valid}}
  <v-form
    ref="form"
    v-model="valid"
   >
    <v-text-field
      v-model="name"
      :counter="10"
      :rules="nameRules"
      label="Name"
      required
    ></v-text-field>

    <v-text-field
      v-model="email"
      :rules="emailRules"
      label="E-mail"
      required
    ></v-text-field>

  
 

    <v-btn
      :disabled="!valid"
      color="success"
      class="mr-4"
      @click="save"
    >
      Save
    </v-btn>

    <v-btn
      color="error"
      class="mr-4"
      @click="reset"
    >
      Reset  
    </v-btn>

  
  </v-form>
</div>
</template>

<script>
 
  export default {
    name: 'PlanForm',

    data: () => ({
      valid: true,
      name: '',
      nameRules: [
        v => !!v || 'Name is required',
        v => (v && v.length <= 10) || 'Name must be less than 10 characters',
      ],
      email: '',
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
      ],
      
      
    }),
     methods: {
      save () {
        if(this.$refs.form.validate()){
            const aes=   this.$store.getters.getAesKey
            const user=this.$store.getters.getUser
            const plan={userName:user.username,name:this.name,email:this.email}
            this.$store.dispatch("addUserPlan",plan)
            this.$refs.form.reset()
            
        }
      },
      async reset () {
       
           this.$refs.form.reset()
           this.$refs.form.resetValidation()
        
      },
      
    },
  }
</script>
