<template> 
  <v-card id="plan">
     <div class="text-center mt-2">
      <v-progress-circular
        indeterminate
        class="primary--text"
        :width="7"
        :rotate="90"
        :size="70"
        v-if="loading"
        :flex="12"
      ></v-progress-circular>
    </div>
    <v-card-title>
     PLAN
    </v-card-title>

    <v-card-subtitle>
     {{!plan? "" :plan.userName  }}
    </v-card-subtitle>
     <v-card-text class="text--primary">
      <div>{{plan.name}}</div>

      <div>{{plan.email}}</div>
    </v-card-text>
    <v-card-actions>
      <v-btn
        color="red lighten-2"
        text
        @click= "generatePDF"
      >
        Generate PDF<v-icon>mdi-file-pdf</v-icon>
      </v-btn>

      <v-spacer></v-spacer>

      
    </v-card-actions>

  
  </v-card>
 
   
</template>
  
 

<script>
import { jsPDF } from "jspdf";
import Loading from "@/assets/Loading_icon.gif";
export default {
props:['plan','loading'],
name: "Plan",
  data: () => ({
     
  }),
  computed:{
     Loading() {
      return Loading;
    }
    /*   plan(){
          return this.$store.getters.getPlan
      } */
  },methods:{
    generatePDF(){
      const doc = new jsPDF();

doc.text(`PLAN for: ${this.plan.userName}`, 10, 10)
 
doc.text(`Name: ${this.plan.name}`, 10, 15)
 
doc.text(`Email: ${this.plan.email}`, 10, 20)
 


doc.save(`${this.plan.userName}-plan.pdf`);
    }
  }, 
 
}
</script>

