<template>
  <div>
    <v-container>
      <h2  >Subscribed to Plan</h2>
      <v-row>
        <v-col xs="10" md="6"   class="  mx-auto justify-center">
         <Plan v-bind:plan="plan"/>
        </v-col>
        <v-col xs="10" md="6"   class="  mx-auto justify-center">
        <v-text-field  class="pb-2"
          required
          hide-details
          outlined
          v-model="subscriberId"
          :rules="subscriberRules"
          placeholder="add subscriber blockstack id "
          append-icon="mdi-check"
          @click:append="getUserPlan"
          clearable
        >
        </v-text-field>
        </v-col>          
      </v-row>
      
        
    </v-container>
  </div>
</template>

<script>
import Plan from "@/components/Plan.vue" 
import PlanForm  from "@/components/PlanForm.vue"
import PlanSubcriberForm from "@/components/PlanSubcriberForm.vue"
export default {
 
 
  name: "SubscribedPlan",
    components: { Plan ,PlanForm,PlanSubcriberForm},
  data() {
    return {
     plan:{
         name:"",
         email:"",
         userName:""
     },
      subscriberId: '',
      subscriberRules: [
        v => !!v || 'Blockstack id is required',
        v => /.+\.id\.+(blockstack)/.test(v) || 'Must be valid Blockstack id',
      ]
    };
  },
  methods: {

  
    async getUserPlan(){
         if(!this.subscriberId ) return 
        const user=this.$store.getters.getUser
        const userPlan=await this.$store.dispatch("getsubscribedPlan",{username:user.username,subcribed:this.subscriberId})
       this.plan=userPlan
    }
   
  },
  computed: {
  
    /*  logo() {
      return logo;
    }, */
    /* landingimg() {
      return landingimg;
    } */
    
   
    
  },
   async mounted() {
    
  }
   
};
</script>

<style scoped>
.landing-style {
  text-align: left;
  color: black; /*  #343a40 !important; */
}
</style>
