import Vue from 'vue'
import App from './App.vue'
import router from "./router/router";
import { store } from "./store/store";
import vuetify from './plugins/vuetify';
import { Person } from "blockstack";
import { userSession } from "./auth";
 
Vue.config.productionTip = false

new Vue({
  router,
  store,
  vuetify,
  render: h => h(App),
  async created() {
 
    if (userSession.isUserSignedIn()) {    
      
      let user;
      this.userData = userSession.loadUserData();
      user = new Person(this.userData.profile);      
      user.username = this.userData.username; 
      const isPublicKeySet=await this.$store.dispatch("isFileExist",{path:'key.json',isPrivate:false  })
      //await this.$store.dispatch("deletekeys")
      if(!isPublicKeySet){
        console.log("public key")
        await this.$store.dispatch("setupPublicKey")
       
      }  
      
     const isAESKeySet=await this.$store.dispatch("isFileExist",{path:`shared/AES.json`,isPrivate:false  })
     console.log(isAESKeySet)
     if(!isAESKeySet){
        console.log("create AES")
        await this.$store.dispatch("setupAESKey",{username:this.userData.username})
     } else{
      console.log("load AES")
        await this.$store.dispatch("loadAESKey",{username:this.userData.username})
       
     }
     await this.$store.dispatch("getSubscribers")
    
      this.$store.commit({
        type: "setUser",
        user
      });
      await this.$store.dispatch("getUserPlan",{username:user.username})
    } else if (userSession.isSignInPending()) {
      
      //////////////
      userSession.handlePendingSignIn().then(async userData => {
        window.location = window.location.origin;
        let user;
         userData = userSession.loadUserData();  
         user = new Person(userData.profile);     
        user.username = userData.username;
        const isPublicKeySet=await this.$store.dispatch("isFileExist",{path:'key.json',isPrivate:false  })
        
        if(!isPublicKeySet){
          await this.$store.dispatch("setupPublicKey")
        }
        // check if AES is set
        const isAESKeySet=await this.$store.dispatch("isFileExist",{path:`keys/${this.userData.username}.json`,isPrivate:false  })
         
        if(!isAESKeySet){
          await this.$store.dispatch("setupAESKey",{username:this.userData.username})
        } 
  
        await this.$store.dispatch("getSubscribers")
      this.$store.commit({
        type: "setUser",
        user
      });
      });
    }

    
  }
}).$mount('#app')

 


 

 
 
 
 
 
