<template>
 <v-row>
         <v-col xs="10" md="10"   class="    justify-center">   
        <v-text-field  class="pb-2"
          required
          hide-details
          outlined
          v-model="subscriberId"
          :rules="subscriberRules"
          placeholder="add subscriber blockstack id "
          append-icon="mdi-plus"
          @click:append="save"
          clearable
        >
        </v-text-field>
        
               <v-list   >
      
         <div v-for="subscriber in subscribers" :key="subscriber.username" >
        <v-list-item class="ma-5">
          <template v-slot:default >        

            <v-list-item-content>
              <v-list-item-title>{{subscriber.username}}</v-list-item-title>
              
            </v-list-item-content>
              <v-list-item-action>
          <v-btn icon @click.stop="revoke(subscriber.username)">
            <v-icon color="red lighten-2">mdi-delete</v-icon>
          </v-btn>
        </v-list-item-action>
          </template>
        </v-list-item>
    <v-divider></v-divider>
          </div>
         
      
    </v-list>
          
          
 
     </v-col>
      </v-row>   
</template>

<script>
 
  export default {
    name: 'PlanSubcriberForm',

    data: () => ({
      valid: true,
      name: '',
      nameRules: [
        v => !!v || 'Name is required',
        v => (v && v.length <= 10) || 'Name must be less than 10 characters',
      ],
      subscriberId: '',
      subscriberRules: [
        v => !!v || 'Blockstack id is required',
        v => /.+\.id\.+(blockstack)/.test(v) || 'Must be valid Blockstack id',
      ],
      
      
    }),


 methods: {

  
    async deleteAll(){
       await this.$store.dispatch("deletekeys",{username:"moh_elb01.id.blockstack"})
    },
     save () {
         
             
            if(!this.subscriberId ) return 
            if(this.subscribers.map(subscriber=>{return subscriber.username.toLowerCase()} ).includes(this.subscriberId)) {
              alert("already exist")
              return
            }
            const subscriber={userName: this.subscriberId}
            this.$store.dispatch("addSubscriber",this.subscriberId)
           
      },
     
        async  revoke(username){
      
     await this.$store.dispatch("revokeSubscriber",{username})


    },
   
  },
  computed: {
  
    
    
    
    subscribers(){
        const subscribers=this.$store.getters.getSubscribers
    

      return subscribers;
    }
  },
   async mounted() {
    
  }

 
  }
</script>
