 import { userSession } from "../../auth";
export default {
  isUserSignedIn(state) {
    return userSession.isUserSignedIn();
  },
  getUser(state) {
    return state.user;
  },
  getAesKey(state) {
 
    return state.aesKey;
  },
  getSubscribers(state) {
    return state.subscribers;
  },
  getPlan(state){
    return state.plan;
  }
   
};
