async revokeSubscriber({commit,state},payload) {
   
        try{
            const  subscribers =state.subscribers;
            const username=state.user.username
            const currentAesKey=state.aesKey
            let decrptedPaln
            let AESKey
        
            if(userSession.isUserSignedIn()) {
      
                const data=await storage.getFile(`plans/plan.json` 
                    )
                if(data){

                    const simpleEncyrpt = new SimpleCryptoJS(state.aesKey)            
                    const encryptedFile =JSON.parse(data) //JSON.parse(data)
                    
                    decrptedPaln=await simpleEncyrpt.decrypt(encryptedFile )
                   
                    
                    
                     
                }else{
                    return
                } 
            } 
            
             
             AESKey=await  _setupAESKey(username)
            const simpleEncyrpt = new SimpleCryptoJS(AESKey) 
            const encypteFile1=simpleEncyrpt.encrypt(JSON.stringify(decrptedPaln))            
            await storage.putFile(`plans/plan.json`, JSON.stringify(encypteFile1))                              
            commit("setPlan",decrptedPaln)
            subscribers.forEach(async subscriber=>{
            try{               
                
                if(subscriber.username !=payload.username){
                    const keyData =await storage.getFile('key.json', {
                        username: subscriber.username,decrypt:false
                    })
                   if(keyData ) {                        
                        const publicKey=JSON.parse(keyData)                        
                        const encryptedKey = encryptECIES( publicKey, AESKey)
                        await storage.putFile(`shared/${subscriber.username}.json`, JSON.stringify(encryptedKey),{encrypt:false})
                               
                    }       
                    
                }
            }catch(err){
                console.log(err.message)
            }
            })           

            const newSubscribers=subscribers.filter(  subscriber=>{              
                        return subscriber.username !=payload.username    
               
            })     
            console.log("new",newSubscribers)
            await storage.putFile('subscribers.json', JSON.stringify(newSubscribers || []),{
                encrypt: true  
                
            });
            commit("SetSubscribers",  {subscribers:newSubscribers})
            commit("SetAESKey", {aesKey:AESKey})
        }
        catch(err){
            console.log(err.message)
        }
    
    },

async removeAllSubscribers() {
        try{
            const {subscribers}=state;
            const {username}=state.user
            
            await setupAESKey({username})
            subscribers.forEach(async subscriber=>{
            try{
                await storage.deleteFile(`shared/${subscriber.username}.json`)                
            }catch(err){
                console.log(err.message)
            }
            })             
            await storage.putFile('subscribers.json', '[]') 
                commit("SetSubscribers",   {subscribers:[]});         

        }
        catch(err){console.log(err.message)}
       
    },
 
async addSubscriber ({commit,state},newSubscriber) {
 
    
        try{
           const subscribers =state.subscribers;
           if(subscribers.map(subscriber=>{return subscriber.username.toLowerCase()} ).includes(newSubscriber)) return
         
           const keyData =await storage.getFile('key.json', {
            username: newSubscriber,decrypt:false
            })
            
           if(keyData ) {
             
           
               
                const aesKey=state.aesKey
                
                const publicKey=JSON.parse(keyData)
                
                subscribers.push({username: newSubscriber, publicKey: publicKey})
                const encryptedKey =await encryptECIES( publicKey, Buffer.from(aesKey, 'utf8'))
                
                await storage.putFile(`shared/${newSubscriber}.json`, JSON.stringify(encryptedKey),{encrypt:false})
                await storage.putFile('subscribers.json',JSON.stringify( subscribers ),{encrypt:true})
                
                commit("SetSubscribers",  {subscribers: subscribers })
                   
                       
            }            
        }catch(err){
            console.log(err.message)
        }
       
    },
