async addUserPlan({commit,state},payload){
    try{
        const simpleEncyrpt = new SimpleCryptoJS(state.aesKey)
        
        const encypteFile=simpleEncyrpt.encrypt(JSON.stringify(payload))
       
        await storage.putFile(`plans/plan.json`, JSON.stringify(encypteFile),{encrypt:false}) 
        commit("setPlan",payload)
        
    }catch(err){
        console.log(err.message)
    }
   
    
},

