<template>
  <v-app dark class="light-green lighten-5">
    <div>
      <v-navigation-drawer app v-model="drawer" v-if="signedIn" class="light-green lighten-5">
        <v-list-item>
          <v-list-item-avatar>
            <v-img :src="user.avatar ? user.avatar : avatar"></v-img>
          </v-list-item-avatar>

          <v-list-item-content>
            <v-list-item-title>
              <span>{{ user.username ? user.username : "" }}</span>
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>

        <v-divider></v-divider>

        <v-list dense>
          <v-list-item
            class="text--blue"
            v-for="item in items"
            :key="item.title"
            link
            :to="item.link"
          >
            <v-list-item-icon>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>{{ item.title }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item class="text--blue" @click="signOut" link>
            <v-list-item-icon>
              <v-icon>mdi-logout</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>Signout</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list>
      </v-navigation-drawer>
      <v-app-bar app class="primary" dark>
        <v-app-bar-nav-icon v-if=" signedIn" @click.stop="drawer = !drawer"></v-app-bar-nav-icon>
        <v-toolbar-title>
          <router-link
            to="/"
            class="white--text"
            tag="span"
            style="cursor:pointer"
          >Blockstack-starting</router-link>
        </v-toolbar-title>
        <v-spacer></v-spacer>

        <v-btn v-if="!signedIn" text @click="signIn">
          <v-icon left>mdi-login</v-icon>Sign in with blockstack
        </v-btn>
        <v-chip
          pill
          v-if="signedIn"
          class="primary darken-3"
          
        >
          <v-avatar left class="text-uppercase">
            <img :src="user.avatar ? user.avatar :  avatar" />
          </v-avatar>
          {{ user.username ? user.username : "" }}
        </v-chip>
        <v-btn text v-if="signedIn" @click="signOut" class="ml-2">
          Sign out
          <v-icon right>mdi-logout</v-icon>
        </v-btn>
      </v-app-bar>
    </div>

    <v-content class="light-green lighten-5">
      <router-view></router-view>
    </v-content>
  </v-app>
</template>

<script>
import { userSession ,authenticate} from "./auth";
import avatar from "@/assets/avatar-placeholder.png";
export default {
  name: "App",

  computed: {
    signedIn() {
       return this.$store.getters.isUserSignedIn;
    },
    avatar() {
       return avatar;
    },
    user() {
      //to add computed  avatar and name
      let givenName;
      let username;
      let avatar = "https://s3.amazonaws.com/onename/avatar-placeholder.png";
      let User = { givenName, avatar, username };
      if (this.signedIn) {
        User = this.$store.getters.getUser;

        //  console.log(User);
        givenName = User ? User.name() : "Nameless Person";
        username = User ? User.username : null;
        console.log(givenName,username)
        avatar = User
          ? User.avatarUrl()
          : "https://s3.amazonaws.com/onename/avatar-placeholder.png";
        //  console.log("kkkkk", avatar);
        User = { ...User, givenName, avatar, username };
      }
      
      return User; 
    }
  },
  methods: {
    signIn() {
      authenticate();
    },
    signOut() {
      this.$store.dispatch("signOut");
    }
  },
  data() {
    return {
      drawer: false,
      dialog: false,
      items: [
        { title: "Dashboard", icon: "mdi-view-dashboard", link: "/dashboard" },
        { title: "SubscribedPlan", icon: "mdi-account-group-outline", link: "/subcribed" },
        
      ],
      signin_toggle: false
    };
  },
  mounted() {
    
  }
};
</script>

